@import "content_page.css";

body:before {
    content: "";
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-size: 100% 100%;
    background-position: 0 0;
    background-image: repeating-linear-gradient(291deg, #01022D2E 92%, #073AFF00 100%), repeating-radial-gradient(75% 75% at 228% 195%, #B4449E12 30%, #00166214 39%), radial-gradient(99% 99% at -20% 2%, #003355FF 0%, #073AFF00 100%), radial-gradient(99% 99% at -10% 0%, #2A0040FF 0%, #073AFF00 100%), radial-gradient(160% 154% at 256px -891px, #003546FF 0%, #16002EFF 100%);
}